const data = (state={}, action) => {
  switch(action.type) {
    case 'CURRENT_DATA': {
      if(action.payload.status === 200) {
        const currentData = action.payload.data;
        const currentDataObject = currentData.reduce((obj, di) => {
          obj[di.dataItemId] = {...di};
          return obj;
        }, {});
        return {
          ...state,
          currentData: {
            array: currentData,
            object: currentDataObject
          }
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          currentData: {
            array: [],
            object: {}
          }
        };
      }
    }
    case 'DATAITEM_DEFINITIONS': {
      if(action.payload.status === 200) {
        const dataItemDefinitions = action.payload.data;
        const dataItemDefinitionsObject = dataItemDefinitions.reduce((obj, di) => {
          obj[di.dataItemId] = { ...di };
          return obj;
        }, {});
        return {
          ...state,
          dataItemDefinitions: {
            array: dataItemDefinitions,
            object: dataItemDefinitionsObject
          }
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          dataItemDefinitions: {
            array: [],
            object: {}
          }
        };
      }
    }
    case 'ATTRIBUTES_DATA': {
      if(action.payload.status === 200) {
        const attributes = action.payload.data;
        const attributesObject = attributes.reduce((obj, attr) => {
          obj[attr.attributeName] = {...attr};
          return obj;
        }, {});
        return {
          ...state,
          attributes: {
            array: attributes,
            object: attributesObject
          }
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          attributes: {
            array: [],
            object: {}
          }
        };
      }
    }
    case 'ASSET_DATA': {
      if(action.payload.status === 200) {
        return {
          ...state,
          asset: action.payload.data
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          asset: null
        };
      }
    }
    case 'ASSET_DEFINITION_DATA': {
      if(action.payload.status === 200) {
        return {
          ...state,
          assetDef: action.payload.data
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          assetDef: null
        };
      }
    }
    default:
      return state;
  }
}

export default data;
